import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';
import { Table } from '../components/Table';

export default {
  title: 'WORKOUT/Series',
  component: Table,
};

export const Template = args => {
  const [_data, setData] = useState();

  useEffect(
    () =>
      setData([
        [1, '2022-11-23', 15, 100],
        [2, '2022-11-23', 10, 8],
      ]),
    [],
  );
  const onItemChange = item => {
    console.log(item);
    setData(
      _data.map(serie => {
        if (serie.id.value === item.id.value) {
          return item;
        }
        return serie;
      }),
    );
  };

  return <Table onItemChange={onItemChange} data={_data} {...args} />;
};;

export const Monday = Template.bind({});
Monday.args = {
  title: 'Curl biceps',
  muscleGroup: 'Biceps',
  schema: [
    { name: 'id', type: 'text' },
    { name: 'date', type: 'date' },
    { name: 'repetitions', type: 'number' },
    { name: 'weight', type: 'number' },
  ],
  data: [
    [1, '2022-11-23', 15, 100],
    [2, '2022-11-23', 10, 8],
  ],
  max: 5,
  min: 1,
  handleOnSave: data => {
    console.log('saving');
    console.log(data);
  },
};

